/* src/App.css */
body {
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f2f2f2;
}

.age-calculator-container {
  text-align: center;
}

h1 {
  margin-bottom: 20px;
}

.dob-input {
  margin-bottom: 20px;
}

.calculate-button {
  background-color: #2196f3; /* Blue color */
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
}

.calculate-button:hover {
  background-color: #1565c0; /* Darker shade of blue on hover */
}
